      <mt-button icon="more" slot="right"></mt-button>
    </mt-header>
    <img src="../../../../static/img/my/qianbaobg.png" style="width: 100%;height: auto">
    <div class="text" style="text-align: center">
      <div class="" style="font-size: 14px;color: #282828;margin-top: 30px">我的余额(元)</div>
      <div style="color: #FF6060;font-size: 18px;font-weight: bold;font-family: PingFang-SC-Heavy;margin: 10px 0">888.81</div>
      <el-button type="primary" style="width: 80%;margin: 10px 0"  @click="go({path: '/recharge'})">充 值</el-button>
      <el-button type="warning" style="width: 80%;margin: 10px 0"  @click="go({path: '/forward'})">提 现</el-button>

    </div>
  </div>

</template>

<script>
  export default {
    data () {
      return {

      }
    },
    methods:{
      go(e){
        this.$router.push(e)
      },
    },
    mounted(){

    }
  }
</script>


<style scoped>

